<template>
<div>
  <sub-title title="" :link="[{name:'',to: '/center/manHome'},{name:'',to:''}]"></sub-title>
  <div class="dblock">
    <h1 class="dtitle"></h1>
    <Table :columns="columns1" :data="data1" width="500"></Table>
    <chart :options="pie1" class="pie" id="pie1" ref="pie11"></chart>
  </div>
  <div class="dblock">
    <h1 class="dtitle"></h1>
    <chart :options="bar1" class="bar"></chart>
  </div>
  <div class="dblock">
    <h1 class="dtitle">ERP</h1>
    <Table :columns="columns2" :data="data2" width="500"></Table>
    <chart :options="pie2" class="pie" ></chart>
  </div>
  <div class="dblock">
    <h1 class="dtitle"></h1>
    <Table :columns="columns3" :data="data3" width="500"></Table>
    <chart :options="pie3" class="pie" ></chart>
  </div>
</div>
</template>

<script>
import $ from 'jquery'
export default {
  name: "index",
  data(){
    return{
      columns1:[
        {title:'',key:'type'},
        {title:'',key:'sum'},
        {title:'',key:'count'},
        {title:'',key:'odds'},
      ],
      data1:[
        {type:'', sum:'', count:'', odds:''},
        {type:'', sum:'', count:'', odds:''},
        {type:'', sum:'', count:'', odds:''},
        {type:'', sum:'', count:'', odds:''},
        {type:'', sum:'', count:'', odds:''},
        {type:'', sum:'', count:'', odds:''}
      ],

      pie1:{
        title: {
          text:'',
          left: 'center'
        },
        tooltip: {
          trigger: 'item',
          formatter: "{a} <br/>{b}: {c} ({d}%)"
        },

        color:['#6eb4f2','#fdc12f','#82d03c'],
        series: [{
          name:'',
          type:'pie',
          radius: ['50%', '65%'],
          avoidLabelOverlap: false,
          label: {
            normal: {
              show: false,
              position: 'center'
            },
            emphasis: {
              show: false,
              textStyle: {
                fontSize: '16',
                fontWeight: ''
              }
            }
          },
          labelLine: {
            normal: {
              show: false
            }
          },
          data:[
            {value:4380, name:''},
            {value:1620, name:''},
          ]
        }],
        // legend: {
        //   orient: 'vertical',
        //   // x: 'left',
        //   bottom: 0,
        //   data:['','']
        // },
      },

      bar1:{
        color: ['#6eb4f2'],
        tooltip : {
          trigger: 'axis',
          axisPointer : {            // 
            type : 'shadow'        // 'line' | 'shadow'
          }
        },
        grid: {
        },
        xAxis : [
          {
            type : 'category',
            data : ['', '', '', '', '',  '', '', '', '', '', '', '', '', '',  '', ''],
            axisTick: {
              alignWithLabel: true
            },
            axisLabel: {
              interval: 0,
              rotate: 40
            },
          }
        ],
        yAxis : [
          {
            type : 'value'
          }
        ],
        series : [
          {
            label: {
              normal: {
                show: true,
                position: 'top'
              }
            },
            name:'',
            type:'bar',
            //barWidth: '60%',
            data:[6, 7, 6, 8, 5,  10, 6, 88, 7, 14, 24, 6, 9, 5, 4, 9]
          }
        ]
      },

      columns2:[
        {title:'(9)',key:'fin'},
        {title:'(7)',key:'on'},
        {title:'(8)',key:'never'},
      ],
      data2:[
        {fin:'', on: '', never: ''},
        {fin:'', on: '', never: ' JAC DMS'},
        {fin:'', on: '', never: ''},
        {fin:'', on: '', never: ''},
        {fin:'', on: '', never: 'ERP'},
        {fin:'', on: 'Autoplus', never: 'ERP'},
      ],

      pie2:{
        title: {
          text:'ERP',
          left: 'center'
        },
        tooltip: {
          trigger: 'item',
          formatter: "{a} <br/>{b}: {c} ({d}%)"
        },
        legend: {
          orient: 'vertical',
          // x: 'left',
          bottom: 0,
          data:['','','']
        },
        color:['#6eb4f2','#fdc12f','#82d03c'],
        series: [{
          name:'',
          type:'pie',
          radius: ['50%', '65%'],
          avoidLabelOverlap: false,
          label: {
            normal: {
              show: false,
              position: 'center'
            },
            emphasis: {
              show: false,
              textStyle: {
                fontSize: '16',
                fontWeight: ''
              }
            }
          },
          labelLine: {
            normal: {
              show: false
            }
          },
          data:[
              {value:9, name:''},
              {value:7, name:''},
              {value:8, name:''},
          ]
        }]
      },

      columns3:[
        {title:'',key:'no'},
        {title:'',key:'name'},
        {title:'()',key:'times'},
      ],
      data3:[
        {no:'1', name:'', times:'0'}
        ],

      pie3:{
        title: {
          text:'',
          left: 'center'
        },
        tooltip: {
          trigger: 'item',
          formatter: "{a} <br/>{b}: {c} ({d}%)"
        },
        legend: {
          orient: 'vertical',
          // x: 'left',
          bottom: 0,
          data:['','','']
        },
        color:['#6eb4f2','#fdc12f','#82d03c'],
        series: [{
          name:'',
          type:'pie',
          radius: ['50%', '65%'],
          avoidLabelOverlap: false,
          label: {
            normal: {
              show: false,
              position: 'center'
            },
            emphasis: {
              show: false,
              textStyle: {
                fontSize: '16',
                fontWeight: ''
              }
            }
          },
          labelLine: {
            normal: {
              show: false
            }
          },
          data:[
            {value:0, name:''},
            {value:0, name:''},
            {value:0, name:''},
          ]
        }]
      },
    }
  },
  mounted(){
    let self= this, acctok= localStorage.getItem("ACCESSTOKEN");
    //
    this.axios({
      method: 'get',
      url: '/statistics/admin/adminStatistics/'+ acctok,
      headers: {
        'Content-type': 'application/json'
      },
    }).then(function (res) {
      console.log(res)
      let data= res.data.statisticsDataPO,td= self.data1
      td[0].sum= data.repairCompanySum; td[0].count= data.repairCompany; td[0].odds= data.repairCompanyrate+"%";
      td[1].sum= data.companyISum; td[1].count= data.companyIcount; td[1].odds= data.companyIrate+"%";
      td[2].sum= data.companyIISum; td[2].count= data.companyIIcount; td[2].odds= data.companyIIrate+"%";
      td[3].sum= data.companyIIISum; td[3].count= data.companyIIIcount; td[3].odds= data.companyIIIrate+"%";
      td[4].sum= data.carCompanySum; td[4].count= data.carCompanyCount; td[4].odds= data.carCompanyrate+"%";
      td[5].sum= data.motorCompanySum; td[5].count= data.motorCompanyCount; td[5].odds= data.motorCompanyrate+"%";

      self.pie1.series[0].data=[
        {value:parseInt(data.repairCompany), name:''},
        {value:parseInt(data.repairCompanySum) - parseInt(data.repairCompany), name:''},
      ]
      // self.$refs.pie11.mergeOptions(self.pie1)
      $("#pie1").append("<div style='position: absolute;left: 75px;top: 130px;font-size: 28px'>"+data.repairCompanySum+"</div>")
    })
  },
  methods:{

  }
}
</script>

<style scoped lang="scss">
.pie {
  width: 300px;
  height: 300px;
  position: relative;
}
.bar{
  width: 600px;
  height: 300px;
}
</style>



