<template>
<div>
  <sub-title v-if="userType()==3" title=""
             :link="[{name:'',to: '/center/manHome'},{name:'',to:''}]"></sub-title>
  <sub-title v-if="userType()==2" title=""
             :link="[{name:'',to: '/center/comHome'},{name:'',to:''}]"></sub-title>
  <sub-title v-if="userType()==1" title=""
             :link="[{name:'',to: '/center/perHome'},{name:'',to:''}]"></sub-title>
  
</div>
</template>

<script>
export default {
  name: "e-record-list",
  methods:{
    userType(){
      return this.$store.getters.userType
    },
  },
}
</script>

<style scoped>

</style>



