<template>
<div>
<div class="topbar">
  <div v-if="!userName()" class="greet"><span></span>
    <router-link tag="a" :to="{ name: 'login'}"></router-link>|
    <router-link tag="a" :to="{ name: 'register'}"></router-link>
  </div>
  <div v-if="userName()" class="greet"><span></span><span>{{userName()}}</span>
    <router-link tag="span" :to="{ name: 'perHome'}" v-if="userType()==1" class="button"></router-link>
    <router-link tag="span" :to="{ name: 'comHome'}" v-if="userType()==2" class="button"></router-link>
    <router-link tag="span" :to="{ name: 'manHome'}" v-if="userType()==3" class="button"></router-link>
    <a @click="logout"></a>
  </div>
</div>
<div class="midbar">
  <img class="logo" src="../assets/header/logo1.png">
  <div class="title">
    <p></p><p>Shanghai Automobile Maintenance public Service  Platform</p>
  </div>
  <div class="wx" @mouseover="showqr" @mouseout="hideqr">
    <img src="../assets/header/vx.png"><span></span>
  </div>
  <div class="app"><img src="../assets/header/sj.png"><span>APP</span></div>
  <img class="qrcode" src="../assets/header/lantoShare.jpg" v-show="qr" @mouseover="showqr" @mouseout="hideqr"/>
</div>
<div class="comp">
  <div class="left"><img src="../assets/header/gh.png"/><div></div></div>
  <div class="mid"></div>
  <div class="right"></div>
</div>

  <Menu mode="horizontal" theme="primary" :active-name="this.$route.path" @on-select="select">
    <MenuItem name="/index"></MenuItem>
    <MenuItem name="2"></MenuItem>
    <MenuItem name="3"></MenuItem>
    <Submenu name="4">
      <template slot="title"></template>
      <MenuItem name="4-1"></MenuItem>
      <MenuItem name="4-2"></MenuItem>
      <MenuItem name="4-3"></MenuItem>
      <MenuItem name="4-4"></MenuItem>
    </Submenu>
    <MenuItem name="5"></MenuItem>
    <Submenu name="6">
      <template slot="title"></template>
      <MenuItem name="6-1"></MenuItem>
      <MenuItem name="6-2"></MenuItem>
    </Submenu>
    <Submenu name="7">
      <template slot="title"></template>
      <MenuItem name="7-1"></MenuItem>
      <MenuItem name="7-2"></MenuItem>
      <MenuItem name="7-3"></MenuItem>
      <MenuItem name="7-4"></MenuItem>
    </Submenu>
    <MenuItem name="/about"></MenuItem>
  </Menu>

</div>
</template>

<script>

export default {
  data(){
    return{
      qr: false,
    }
  },
  computed:{

  },
  methods:{
    userName(){
      let name= this.$store.getters.userName, localInfo= localStorage.getItem('USERINFO')
      if (!name){
        if (localInfo){
          this.$store.commit("putUserInfo",JSON.parse(localInfo))
        }
      }
      return this.$store.getters.userName
    },
    userType(){
      return this.$store.getters.userType
    },
    logout(){
      let self= this
      this.$Modal.confirm({
        title: '',
        content: '',
        onOk: () => {
          this.$Message.success('');
          localStorage.removeItem("ACCESSTOKEN");
          localStorage.removeItem("USERINFO");
          self.$store.commit("putUserInfo",{})
          self.$router.replace({
            path: '/login',
          });
        },
        onCancel: () => {
//          this.$Message.info('Clicked cancel');
        }
      });
    },
    showqr(){
      this.qr=true
    },
    hideqr(){
      this.qr=false
    },
    select(name){
      console.log(name)
      this.$router.push({
        path: name
      })
    }
  }

}
</script>

<style scoped lang="scss">
.topbar{
  width: 100%;
  min-width: 800px;
  height: 40px;
  line-height: 40px;
  font-size: 14px;
  background-color: #f2f2f2;
  overflow: hidden;
  .greet{
    float: right;
    margin-right: 10px;
    a{
      padding: 0 5px;
    }
    .button{
      background: #4ba7f5;
      color: white;
      padding: 2px 5px;
      line-height: 40px;
      text-align: center;
      cursor: pointer;
    }
  }
}
.midbar{
  margin-top: 20px;
  position: relative;
  min-width: 800px;
  .logo{
    width: 60px;
    margin: 0 20px;
  }
  .title{
    display: inline-block;
    p{
      font-size: 30px;
    }
    p:last-child{
      font-size: 15px;
      margin-top: 5px;
    }
  }
  .app,.wx{
    float: right;
    margin-right: 20px;
    font-size: 14px;
    height: 33px;
    overflow: hidden;
    margin-top: 10px;
    img{
      display: block;
      float: left;
    }
    span{
      display: block;
      float: left;
    }
  }
  .qrcode{
    position: absolute;
    width: 160px;
    right: 10px;
    top: 30px;
    z-index: 901;
  }

}
.comp{
  width: 800px;
  height: 30px;
  margin: 10px auto;
  text-align: center;
  >div{
    width: 33%;
    float: left;
    height: 30px;
    line-height: 30px;
    font-size: 16px;
  }
  .left{
    img{
      float: left;
    }
    div{
      float: left;
      line-height: 30px;
    }
  }
}

.ivu-menu{
  min-width: 800px;
  height: 40px;
  display: -webkit-flex;
  display: flex;
  justify-content: center;
}
.ivu-menu li{
  height: 40px;
  line-height: 40px;
  padding: 0 18px;
}
</style>




